import React from 'react';
import { FC  } from 'react';
import './MyButton.css'

export interface MyButtonProps {
    label: string,
    disabled?: boolean,
    outline?: boolean,
    size?: 'small' | 'medium' | 'large';
    variant: 'primary' | 'red' | 'yellow' | 'green' | 'violet'
    onClick?: () => void;
}
const MyButton: FC<MyButtonProps> =
    ({
         variant,
         disabled,
         ...props
    }) => {

    const rootClasses = ['my-button']
        variant === 'primary'
            ? rootClasses.push('btn-primary')
            : variant === 'red'
            ?  rootClasses.push('btn-red')
            : variant === 'yellow'
            ?  rootClasses.push('btn-yellow')
            : variant === 'green'
            ? rootClasses.push('btn-green')
            : variant === 'violet'
            ? rootClasses.push('btn-violet')
            : rootClasses.push('btn-disabled')
        //
        // if (variant === 'primary') {
        //     rootClasses.push('btn-primary')
        // } else if (variant === 'red') {
        //     rootClasses.push('btn-red')
        // } else if (variant === 'yellow') {
        //     rootClasses.push('btn-yellow')
        // } else if (variant === 'green') {
        //     rootClasses.push('btn-green')
        // } else if (variant === 'violet') {
        //     rootClasses.push('btn-violet')
        // }

        if (disabled) {
            rootClasses.push('btn-disabled')
        }

    return (
        <button {...props} className={rootClasses.join(' ')}>
            { props.label }
        </button>
    )
}

export default MyButton
